html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
}
html #root,
body #root {
  height: 100%;
}
.App {
  height: 100%;
  display: flex;
  background-color: #58b2dc;
  background-image: url('./assets/texture.png');
  box-sizing: border-box;
}
.aside {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 200px;
  padding: 0 15px;
  box-sizing: border-box;
  overflow: auto;
}
.aside .chart {
  width: 100%;
  height: 120px;
  background: rgba(179, 92, 55, 0.8);
  margin: 10px 0;
  flex-shrink: 0;
  font-size: 30px;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 0 6px rgba(255, 255, 255, 0.5);
}
.content {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}
.content .broad {
  margin: 0;
  padding: 0;
  width: 1000px;
  height: 680px;
  display: flex;
  border: 3px solid #fff;
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.4);
  background: #fff;
}
.content .row {
  flex: 1;
  height: 100%;
  display: flex;
  flex-direction: row;
}
.content .column {
  display: flex;
  flex-direction: column;
  flex: 1;
  height: 100%;
  width: 100%;
}
.content .box {
  flex: 1;
  display: flex;
  border: 1px solid #fff;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-size: 25px;
  background: rgba(179, 92, 55, 0.8);
  position: relative;
}
.content .box > i {
  position: absolute;
  opacity: 0;
}
.content .box .left.active,
.content .box .right.active,
.content .box .top.active,
.content .box .bottom.active {
  opacity: 1;
  background: rgba(236, 184, 138, 0.8);
}
.content .box .left:hover,
.content .box .right:hover,
.content .box .top:hover,
.content .box .bottom:hover {
  opacity: 1;
  background: rgba(236, 184, 138, 0.8);
}
.content .box .left {
  left: 0;
  top: 0;
  bottom: 0;
  width: 30px;
}
.content .box .right {
  right: 0;
  top: 0;
  bottom: 0;
  width: 30px;
}
.content .box .top {
  right: 30px;
  top: 0;
  left: 30px;
  height: 30px;
}
.content .box .bottom {
  right: 30px;
  bottom: 0;
  left: 30px;
  height: 30px;
}
.content .box .cleft.active,
.content .box .cright.active,
.content .box .ctop.active,
.content .box .cbottom.active {
  opacity: 1;
  background: rgba(144, 180, 75, 0.8);
}
.content .box .cleft:hover,
.content .box .cright:hover,
.content .box .ctop:hover,
.content .box .cbottom:hover {
  opacity: 1;
  background: rgba(144, 180, 75, 0.8);
}
.content .box .cleft {
  left: 30px;
  top: 30px;
  bottom: 30px;
  width: 30px;
}
.content .box .cright {
  right: 30px;
  top: 30px;
  bottom: 30px;
  width: 30px;
}
.content .box .ctop {
  right: 30px;
  top: 30px;
  left: 30px;
  height: 30px;
}
.content .box .cbottom {
  right: 30px;
  bottom: 30px;
  left: 30px;
  height: 30px;
}
